.listed-post>header {
    min-width: 16rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 1em;
    padding: 0.3em;
    transition: padding 0.2s;
}

.listed-post>header>h3 {
    color: var(--content-color);
    text-wrap: nowrap;

    overflow: hidden;
}

.listed-post>header>h3>small {
    margin-left: 0.5em;
}

.preview {
    padding: 0.5em;
    height: 8em;
    max-width: 20em;
    overflow: hidden;
}

.preview * {
    font-size: 0.9em;
    font-weight: 400;
    color: var(--content-color);
}

.listed-post:hover .preview * {
    color: var(--content-color);
}